/*****************************************************
	General / Reset / Structure
/*****************************************************/
/* 1em = 10px; */
html {
  font-size: 62.5% !important; }

#page {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: stretch;
  align-items: stretch;
  font-size: 1.4rem; }

main {
  flex-grow: 1; }

a {
  outline: none !important; }

.tel, a[href^="tel"] {
  color: inherit !important;
  cursor: text; }
  .tel:hover, a[href^="tel"]:hover {
    text-decoration: none; }

.clearfix:after {
  content: " ";
  visibility: hidden;
  display: block;
  height: 0;
  clear: both; }

h1,
.like-h1 {
  font-size: 3.8rem;
  text-transform: uppercase;
  margin: 20px 0 10px;
  font-weight: 700; }

h2,
.like-h2 {
  font-size: 2.4rem;
  text-transform: uppercase;
  font-weight: 700;
  margin: 10px 0 15px; }

h3,
.like-h3 {
  font-size: 1.8rem;
  text-transform: uppercase;
  font-weight: 700;
  margin: 10px 0 5px;
  line-height: 1; }

h4,
.like-h4 {
  font-size: 1.8rem;
  text-transform: uppercase;
  font-weight: 700;
  margin: 10px 0 5px;
  line-height: 1; }

p {
  font-size: 1.4rem;
  margin-bottom: 18px; }

/*****************************************************
	Header
/*****************************************************/
#mmenu {
  display: none; }

#open-mmenu {
  display: none; }

#masthead ul {
  list-style: none; }

.home-link {
  display: block; }

#topbar {
  background-color: blue;
  font-size: 0;
  z-index: 1;
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center; }
  #topbar ul {
    list-style: none; }
    #topbar ul a {
      text-decoration: none;
      line-height: 36px;
      text-transform: uppercase;
      -webkit-transition: all 0.5s;
      transition: all 0.5s; }
      #topbar ul a:hover {
        text-decoration: none; }
      #topbar ul a:hover {
        opacity: 0.6; }

.sociaux {
  display: inline-block;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center; }
  .sociaux a {
    font-size: 2rem;
    padding: 0 10px; }
  .sociaux li:last-child a {
    padding-right: 0px; }

/* Menu */
.main-menu {
  background: red;
  z-index: 1;
  position: sticky;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center; }

.navigation {
  width: auto; }
  .navigation ul {
    padding: 0;
    margin: 0;
    font-size: 0; }

.nav-menu li {
  padding: 0 15px;
  margin: 0 10px;
  -webkit-transition: all 0.3s;
  transition: all 0.3s; }
  .nav-menu li:hover {
    text-decoration: none; }
  .nav-menu li a {
    padding: 0;
    font-family: 'Roboto Condensed', sans-serif;
    color: #373431;
    text-transform: uppercase;
    font-weight: 700;
    letter-spacing: 2px;
    font-size: 1.8rem;
    border: 0px solid #f82b3a;
    line-height: 52px;
    height: 52px;
    -webkit-transition: all 0.5s;
    transition: all 0.5s; }
    .nav-menu li a:hover {
      text-decoration: none; }
  .nav-menu li.current-menu-item > a,
  .nav-menu li.current-page-ancestor > a {
    border-bottom: 4px solid #f82b3a; }
  .nav-menu li:hover {
    background-color: #1d1f1e; }
    .nav-menu li:hover > a {
      color: #f82b3a;
      border: none; }

/* MOBILE MENU */
#mmenu {
  background-color: #1d1f1e; }

